.wrapper {
    display: flex;
    justify-content: space-evenly;
    border: 1px solid orange;
    margin: 20px 7px 0 7px;
    border-radius: 7px;
    padding: 10px 0 10px 0;
}

.roundImage {
    display: block;
    width: 45px;
    height: 45px;
    border-radius: 50%;
}

.userWrapper {
    padding: 0;
    margin: 0;
    max-width: 55px;
    text-align: center;
}

.userWrapper:hover .roundImage{
    border: 5px solid orange;
}

.username {
    font-size: .8em;
}

.active {
    color: orange;
}