@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');

body{
  background-color: #000;
}

.AppWrapper {
  margin: 0 auto;
  display: grid;
  grid-template-areas:
    "header header"
    "nav content"
    "footer footer";
  grid-template-rows: 60px 1fr;
  grid-template-columns: .2fr 1fr;
  grid-gap: 10px;
  max-width: 1920px;
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
}

.AppWrapper > * {
  background-color: #282a36;
  color: #f8f8f2;
  border-radius: 7px;
}

.AppWrapperContent {
  grid-area: content;
}