.nav {
    grid-area: nav;
    display: flex;
    flex-direction: column;
}

.navWrapper {
    text-decoration: none;
    color: aqua;
    font-weight: lighter;
    text-align: center;
}

.navWrapper > div {
    padding: 15px;
    margin: 0 15px;
    border-bottom: 1px solid #44475a;
}

.navWrapper:hover {
    font-weight: bold;
    background-color: #44475a;
    cursor: pointer;
    border-bottom: 1px solid #44475a;
}

.navWrapper:hover:nth-child(1) {
    border-top-left-radius: 7px;
    border-top-right-radius: 7px;
}

.navWrapper:hover:nth-last-child(-n + 1) {
    border-bottom-left-radius: 7px;
    border-bottom-right-radius: 7px;
}

.navWrapper.activeLink {
    font-weight: bolder;
    color: orange;
}

.navWrapper.activeLink > * {
    border-bottom: 2px solid orange;
}